
import { ref } from "vue";
import GalleryComponent from "@/components/GalleryComponent.vue";
import Slides from "@/components/Slides.vue";
import CarouselComponent from "@/components/CarouselComponent.vue";

export default {
  name: "AbstractComponent",
  components: {
    GalleryComponent,
    Slides,
    CarouselComponent,
  },
  setup() {
    const images = [
      "a-1",
      "a-2",
      "a-3",
      
      "a-5",
      "a-6",
      "a-7",
      "a-8",
      "a-9",
      "a-10",
      "a-11",
      "a-12",
      "a-13",
    ];
    const showCarouselb = ref(false);
    const imageNumber = ref(0);

    function showCarousel(imagenumber: number) {
      showCarouselb.value = !showCarouselb.value;
      console.log(showCarouselb.value);
      imageNumber.value = imagenumber + 1;
      console.log(imageNumber.value + " image clicked!!");
    }

    return { images, showCarousel, showCarouselb, imageNumber };
  },
};
