
import { defineComponent,ref } from 'vue';
import {HomePage} from '@/SceneFiles/HomePage';
import AbstractComponent from '@/components/AbstractComponent.vue'
import IllustrationsComponent from '@/components/IllustrationsComponent.vue'
import threedComponent from '@/components/3DComponent.vue'
import AboutMe from '@/components/AboutMe.vue'


export default defineComponent({
  name: 'HelloWorld',
  components:{
    AbstractComponent,
    IllustrationsComponent,
    threedComponent,
    AboutMe
  },
  
  props: {
    msg: String,
  },
  mounted(){
    const canvas = document.querySelector("canvas")!;
    new HomePage(canvas);
  },
  setup(){
    const num = ref(1);
    const show = ref(false);
    const amb = ref(false);
    const getb = (value:boolean) =>{
      if(value){
        show.value=!show.value;
      }
      console.log("closeb : " + value);
      amb.value=value;
      
    }
    const inp = (Number:number) => {
      console.log(Number);
      
      if(num.value===Number){
        show.value=!show.value;
        console.log(show.value)
      }
      if(num.value!==Number){
        show.value = true;
      }
      num.value=Number;
      console.log(amb);
    }
    const close = () => {
      show.value=!show.value;
    }
    return {num,inp,show,close,getb};
  }
});
