import {Scene, Engine, ArcRotateCamera, Vector3, MeshBuilder, SceneLoader ,  CubeTexture, HemisphericLight} from '@babylonjs/core';
import '@babylonjs/loaders'

export class HomePage{

    scene : Scene;
    engine : Engine;

    constructor(private canvas:HTMLCanvasElement){
        this.engine = new Engine(this.canvas, true, {preserveDrawingBuffer: true,stencil:true});

        //this.engine.displayLoadingUI();

        this.scene = this.CreateScene();

        this.CreateModels();
        this.engine.runRenderLoop(() => {
            this.scene.render();
        });

        window.addEventListener('resize', () => {
            this.engine.resize();
        })
    }


    CreateScene(): Scene {
        const scene = new Scene(this.engine);
        const camera = new ArcRotateCamera("rotateCamera",Math.PI/2,Math.PI/2,15,new Vector3(0,4,0), this.scene);
        camera.attachControl();

        const envTex = CubeTexture.CreateFromPrefilteredData("environment/environment.env", scene);
        
        scene.environmentTexture = envTex;

        scene.createDefaultSkybox(envTex, true);

        scene.environmentIntensity = 0.5;
        const hemisphere = new HemisphericLight("hemilight", new Vector3(0,1,0),scene);
        // const ground = MeshBuilder.CreateGround("ground", {width : 10, height: 10}, this.scene);
        // const cylinder = MeshBuilder.CreateCylinder("cylinder", {height: 3,diameter:3}, this.scene);
        // const character = SceneLoader.ImportMesh("him","models/","dude.babylon",this.scene);
        return scene;
    }

    async CreateModels() : Promise<void>{
        const models = await SceneLoader.ImportMeshAsync("", "models/","home_page1.glb");
    }
}